$(document).ready(function () {
    //     
    $('a').each(function () {
        //    href
        var hrefValue = $(this).attr('href');

        //   URL 
        var currentPageUrl = window.location.href;

        // ,      
        if (hrefValue === currentPageUrl || hrefValue === '' ) {
            //  ,   <a>  <div>
            $(this).replaceWith(function() {
                return $('<div>', { html: $(this).html(), class: $(this).attr('class') });
            });
        }
    });
});